@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* targets every element*/
.container {
  display: grid;
  grid-template-columns: 100px 100px 100px 100px 100px;
  grid-template-rows: 30px 20px;
  height: 400px;
  background-color:#f4f5f3;
  border-radius: 100px;
  border: 3px solid #893fd3;
  box-shadow: 0px 0px 10px 2px #893fd3;
}
.info{
  color:#0d0d0d;
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 3;
  z-index: 2;
}
.info h1{
  font-weight: 1000;
  font-size: x-large;
  padding-left: 35px;
}
.info p{
  padding-top: 10px;
  font-size: large;
  padding-left: 35px;
}
.image {  
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 2;
  font-weight: 1000;
  z-index: 2;
  width: 250px;
  border-radius: 150px;
  border: 5px solid #384b2d;
}
